'use strict';

var React = require("react");
var Js_dict = require("bs-platform/lib/js/js_dict.js");

function DataAttributesProvider(Props) {
  var data = Props.data;
  var children = Props.children;
  return React.cloneElement(children, Js_dict.fromList(data));
}

var make = DataAttributesProvider;

exports.make = make;
